.Job {
  margin: 0 3vw;
  padding: 1vh 2vw 2vh 2vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.Job:hover {
  -webkit-transform: scale(1.02);
      -ms-transform: scale(1.02);
          transform: scale(1.02);
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
  -webkit-box-shadow: 0px 1px 1px 3px rgba(0, 0, 0, 0.1);
          box-shadow: 0px 1px 1px 3px rgba(0, 0, 0, 0.1);
  cursor: pointer;
}

.job-image {
  width: 13vw;
}

.job-title {
  font-size: 16px;
  font-family: yh;
  color: #555555;
}

@media (max-width: 500px) {
  .job-image {
    width: 23vw;
  }

  .job-title {
    font-size: 13px;
  }

  .Job {
    width: 30vw;
  }
}



